import {UPLOADFILE,CHOICEMENUID,CHOICETYPEMENUID} from './mutation_types'

export default {
  //保存即将上传的文件
  uploadFile({commit},file){
    commit(UPLOADFILE,file)
  },
  //选择菜单ID
  choiceMenuId({commit},id){
    commit(CHOICEMENUID,id)
  },
  //选择资源子菜单ID
  choiceTypeMenuId({commit},id){
    commit(CHOICETYPEMENUID,id)
  },
}



