import {UPLOADFILE,CHOICEMENUID,CHOICETYPEMENUID} from './mutation_types'

export default {
  //获取菜单数组
  [UPLOADFILE](state,file){
    state.uploadFile = file
  },
  //改变菜单ID
  [CHOICEMENUID](state,id){
    state.menuId = id
  },
  //改变资源子菜单ID
  [CHOICETYPEMENUID](state,id){
    state.typeId = id
  },
}



