import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

/*为了解决动态保存查询条件时导致同一个路由多次添加而报错的问题*/
const VueRouterPush = VueRouter.prototype.push
VueRouter.prototype.push = function push (to) {
  return VueRouterPush.call(this, to).catch(err => err)
}

export default new VueRouter({
  routes:[
    {
      path:'/course',
      name:'course',
      component:() => import('../pages/course/index'),
      children:[
        {
          path:'user',
          name:'user',
          component: () => import('../pages/course/user/User'),
          children:[
            {
              path:'main',
              name:'Main',
              component: () => import('../pages/course/user/main/Main'),
            },
            {
              path:'resource',
              name:'Resource',
              component: () => import('../pages/course/user/resource/Resource'),
              children:[
                {
                  path:'files',
                  name:'Files',
                  component: () => import('../pages/course/user/resource/files/files'),
                },
                {
                  path:'filedetail',
                  name:'FileDetail',
                  component: () => import('../pages/course/user/resource/files/detail'),
                },
                {
                  path:'videos',
                  name:'Videos',
                  component: () => import('../pages/course/user/resource/videos/videos'),
                },
                {
                  path:'videotail',
                  name:'VideoDetail',
                  component: () => import('../pages/course/user/resource/videos/detail'),
                },
              ],
              redirect:{name: 'Videos'}
            },
            {
              path:'introduction',
              name:'Introduction',
              component: () => import('../pages/course/user/introduction/Introduction'),
            },
          ],
          redirect:{name: 'Main'}
        },
      ],
       redirect:{name: 'user'}
    },
    {
      path:'/error',
      name:'error',
      component:() => import('../pages/error/Error')
    },
    {
      path:'/',
      redirect:{name:'course'}
    },
    {
      path:'*',
      redirect:{name:'course'}
    },
  ]
})



